* {
  cursor: default;
  font-family: Arial;
}

.block {
  background: lightblue;
  border: black 2px solid;
  margin: 10px;
}

.avatar {
  width: 50px;
  border-radius: 50%;
  border: 2px black solid;
}

textarea {
  cursor: text;
  outline: none;
  -moz-appearance: none;
  border: 1px solid black;
  resize: none;
  background: whitesmoke;
}

.block_up {
  border-top: 2px solid black;
}

.title {
  cursor: default;
  display: inline-block;
  color: black;
  font-size: 30px;
  text-align: center;
  border-bottom: 2px solid black;
}

.button {
  cursor: default;
  display: inline-block;
  border: 1px solid black;
  padding: 10px 20px;
  background: antiquewhite;
}

.app-wrapper {
  display: grid;
  margin: 0 auto;
  max-width: 60%;

  grid-template-areas:
  "h h"
  "n c"
  "n c";

  grid-template-rows: 1fr 8fr 1fr;
  grid-template-columns: 2fr 10fr;
}

.app-wrapper-content {
  grid-area: c;
}

